.App {
  text-align: center;
  background: white;
}

.sidebar,
.contact {
  padding-top: 30px;
  padding-left: 25px;

  -ms-flex: 1 1 auto;

      flex: 1 1 auto;
}

.sidebar {
  text-align: left;
  width: 20%;
}

.contact {
  text-align: right;
  padding-right: 5px;
  width: 20%;
  font-size: 25px;
}

.pics-container {
}

.pic-container {
  background: white;
  margin-top: 30px;
  border-bottom: 1px solid darkgray;
}

.name {
  font-size: 15px;
  font-weight: light;
  margin-bottom:50px;
}

.links {
  font-size: 25px;
}

.link {
}

#mfPreviewBar {
  display: none !important;
}

.pic-box {
}

.pic {
  height: auto;
  width: 100%;
}

.contact-line {
  color: inherit;
  text-decoration: none;
}

.Posters {
  width: 75%;
}

p {
  margin-top: 0px;
}
.caption,
.category {
  text-align: left;

}

.category {
  padding-left: 20px;
}

.caption {
  padding-top: 10px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



